body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Logo-icon {
    color: #d92a41;
    font-weight: bold;
    display: inline-block;
    margin:0 10px;
    font-size:32px;
    -ms-user-select: none;
        user-select: none;
    -moz-user-select: none;
    -khtml-user-select: none;
    -webkit-user-select: none;
    -o-user-select: none;
}
.Navbar-container {
    box-shadow : 0px -2px 4px #b30c22;
    position:fixed;
    top:0;
    left:0;
    right:0;
    height: 60px;
    line-height: 60px;
    background-color: #fff;
}

.logout-btn {
    float: right;
    height: inherit;
    margin: 0;
    border: none;
    outline: 0;
    font-weight: bold;
    margin: 0 10px;
    cursor: pointer;
}
.field-row {
    margin:20px 0;
}

.field-row > label {
    font-weight:bold;
    margin:0 10px;
    width:150px;
    display: inline-block;
    text-align: left;
}
.field-row > input {
    display: inline-block;
    padding:5px;
    width:250px;
}

.submit-btn {
    padding: 15px 30px;
    margin: 0 10px;
    border: none;
    cursor: pointer;
    background-color: #585858;
    color: white;
}

.trash-btn {
    background-color: #c50000;
    margin: 10px 0;
}
.authentication-form {
    position:fixed;
    top:60px;
    bottom:40px;
    left:0;
    right:0;
    overflow-y: scroll;
    margin-bottom:20px;
}
.authentication-form .radio-selection {
    position:absolute;
    height:40px;
    left:0;
    right:0;
    text-align: center;
}
.authentication-form .usertype-selection {
    top:20px;
}
.authentication-form .auth-type-selection {
    top:70px;
}
.selection {
    padding:10px 100px;
    width:100px;
    display:inline-block;
    background-color: #eee;
    cursor: pointer;
}
.selected {
    background-color:#585858;
    color:white;
}
.left-border-curved {
    border-radius:10px 0px 0 10px;
}
.right-border-curved {
    border-radius:0 10px 10px 0;
}

.auth-form {
    position: absolute;
    top: 150px;
    left: 0;
    right: 0;
    text-align: center;
}
.restaurant-detail-container {
    position: absolute;
    top:0;
    bottom:0;
    right:0;
    left:60px;
    padding: 0 10px 10px 10px;
}
.outlet-form-container {
    position: fixed;
    left: 360px;
    top: 60px;
    bottom: 40px;
    width: 100%;
    padding: 10px;
    overflow-y: auto;
}

.delete-outlet-container {
    position:fixed;
    top:60px;
    bottom:40px;
    left:360px;
    padding:10px;
}
.item-form-container {
    position: fixed;
    left: 760px;
    top: 60px;
    bottom: 40px;
    width: 100%;
    padding: 10px;
    overflow-y: auto;
}

.image-unavailable {
    color: #a2a2a2;
    line-height:inherit;
    align-self: center;
    text-align: center;
    width:100%;
}
.delete-item-container {
    position:fixed;
    top:60px;
    bottom:40px;
    left:760px;
    padding:10px;
}
.item-list-tray {
    position:absolute;
    left:300px;
    top:0;
    bottom:0;
    width:400px;
    background-color:#eee;
}

.item-title {
    position:absolute;
    top:0;
    left:0;
    right:0;
    height: 50px;
    background-color: #E8352C;
    color:white;
    margin:0;
    padding:0 10px;
    box-shadow: 0px 1px 2px #333;
    line-height:50px;
    z-index:999;
}

.item-list {
    position:absolute;
    top:50px;
    left:0;
    right:0;
    bottom:0;
    overflow-y: scroll;
}

.item-list-item {
    padding:20px 10px;
}
.item-list-item:hover {
    background-color:#d6d6d6;
}

.item-list-item * {
    color : black;
    margin: 0;
    text-decoration: none;
}

.item-list-item-description {
    font-size: 14px;
    text-align: justify;
    opacity: 0.7;
    margin: 5px 0;
}
.outlet-list-tray {
    position:fixed;
    top:60px;
    bottom:40px;
    left: 60px;
    width:300px;
    background-color:#f9f6f6;
}
.outlet-title {
    position:absolute;
    top:0;
    left:0;
    right:0;
    height: 50px;
    padding:0 10px;
    line-height:50px;
    z-index:999;
    background-color: #d92a41;
    color:white;
    margin:0;
    box-shadow: 0px 1px 2px #333;
}

.outlet-list {
    position: absolute;
    top:50px;
    left:0;
    right:0;
    bottom:0;
    overflow-y: auto;
}
.outlet-list-item {
    padding:20px 10px;
}

.outlet-list-item:hover {
    background-color:#d6d6d6;
}

.outlet-list-item * {
    color : black;
    margin: 0;
    text-decoration: none;
}

.outlet-list-item-detail {
    width: 200px;
    display: inline-block;
}
.nav-tray {
    position: fixed;
    left: 0;
    width: 60px;
    bottom: 40px;
    top: 60px;
    background-color: #a02233;
}


.nav-tray .nav-tray-items .nav-tray-item {
    margin:0;
    width: inherit;
    text-align: center;
    padding:15px 0;
}

.nav-tray .nav-tray-items .nav-tray-item:hover {
    background-color:#c7384c;
}

.nav-tray .nav-tray-items .nav-tray-item * {
    text-decoration: none !important;
    color: #fff;
    font-size:12px;
}

.nav-tray-item-icon {
    font-size:24px !important;
    margin:7px 0;
}
.ContentArea-container {
    position: fixed;
    top:60px;
    bottom:40px;
    left:0;
    right:0;
    overflow-y: scroll;
}

.ContentArea-container > .ContentArea-icon-tray {
    position: absolute;
    top:0;
    bottom:0;
    width:60px;
    background-color:#f0f0f0;
}

.ContentArea-container > .ContentArea-left-pane {
    position: absolute;
    left: 60px;
    width: 400px;
    top: 0;
    bottom: 0;
    background-color:#f7f5f5;
}

.ContentArea-container > .ContentArea-content-pane {
    position: absolute;
    left: 460px;
    right: 0;
    top: 0;
    bottom: 0;
}
.price-whole {
    font-size: 24px;
}
.item-list-entry-container {
    margin:15px 0;
    padding: 10px;
    background-color:#fff;
    box-shadow: 0px 0px 4px rgb(0,0,0,0.3);
}
.item-list-entry-details {
    display: flex;
    flex-direction: row;
}
.item-list-entry-name {
    font-weight:bold;
    display:inline-block;
}
.item-list-entry-image {
    display:grid;
    width:200px;
    min-width:200px;
    max-width:200px;
    margin-right:15px;
}
.item-list-entry-image {
    background-color: rgba(177, 177, 177, 0.1);
}
.item-list-entry-image > img {
    margin:auto;
}
.item-list-entry-description {
    font-size:14px;
    color: #333333;
    font-style: italic;
}
.item-list-entry-container .item-operation-bar {
    margin-top:15px;
}

.item-list-entry-container .item-operation-bar .item-operation-btn {
    border: 1px solid #39cc14;
    outline: none;
    color: #007900;
    padding: 8px 15px;
    border-radius: 5px;
    cursor: pointer;
}
.item-list-entry-container .item-operation-bar .item-operation-btn:hover {
    background-color:rgba(56, 204, 21, 0.3);
}
.tab {
    margin: 0px 5px;
    display: inline-block;
    padding: 2px 9px;
    border-radius: 4px;
    background-color: #d92a41;
    color: white;
    font-size: 14px;
    vertical-align: text-bottom;
}
.list-container {
    margin:20px;   
}

